import React, { useState } from "react";
import FileUpload from "../FileUpload/FileUpload";
import ScanedImage from "../ScanedImage/ScanedImage";
import DefaultImage from "./DefaultImage.jpg";
import Message from "../Message/Message";

function Utilisateur(props) {
  const [message, setMessage] = useState("");
  const [utilisateur, setutilisateur] = useState({
    firstname: "",
    lastname: "",
    username: "",
    password: "",
    repassword: "",
    usertype: "",
    birthday: "",
    email: "",
    phone: "",
    photo_link: DefaultImage,
    remark: "",
    creator: props.getUsername(),
  });

  const setlink = (link, elem) => {
    setutilisateur({
      ...utilisateur,
      [elem]: link,
    });
  };
  const onHandleChange = (event) => {
    setutilisateur({
      ...utilisateur,
      [event.target.name]: event.target.value,
    });
  };

  const onSubmitUtilisateur = (event) => {
    event.preventDefault();
    if (utilisateur.password !== utilisateur.repassword) {
      setMessage("   ");
    } else {
      fetch("http://localhost:3005/Utilisateur", {
        method: "post",
        headers: { "content-type": "application/json" },
        body: JSON.stringify(utilisateur),
      })
        .then((response) => response.json())
        .then((user) => {
          if (user.id) {
            props.loadUser(user);
            props.setAuthent(true);
            props.setUsertype(user.usertype);
            props.history.push("/DisplayForm");
          } else if (user.message === "user alredy exists") {
            setMessage("     ...    ");
          }
        })
        .catch((err) => setMessage(err));
    }
  };

  return (
    <form
      onSubmit={onSubmitUtilisateur}
      className="container form-signin border shadow p-3 my-5 bg-light bg-gradient rounded"
    >
      <h1>   </h1>
      {message ? <Message msg={message} /> : null}
      <div className="row text-right">
        <div className="col order-last">
          <label htmlFor="inputFirstName" className="text-right">
            {" "}
          </label>
          <input
            name="firstname"
            onChange={onHandleChange}
            type="text"
            id="inputFirstName"
            className="form-control text-right"
            placeholder=" "
            autoFocus
          />
        </div>
        <div className="col order-first">
          <label htmlFor="inputLastName" className="text-right">
            
          </label>
          <input
            name="lastname"
            onChange={onHandleChange}
            type="name"
            id="inputLastName"
            className="form-control text-right"
            placeholder=""
          />
        </div>
      </div>
      <div className="row text-right">
        <div className="col">
          <label htmlFor="inputBirthday" className="">
             
          </label>
          <input
            name="birthday"
            onChange={onHandleChange}
            type="date"
            id="inputBirthday"
            className="form-control text-right"
            placeholder=" "
            required
          />
        </div>
        <div className="col order-first">
          <label htmlFor="inputTelephone" className="">
             
          </label>
          <input
            name="phone"
            onChange={onHandleChange}
            type="tel"
            id="inputTelephone"
            className="form-control text-right"
            placeholder=" "
            required
          />
        </div>
      </div>
      <div className="row text-right">
        <div className="col">
          <label htmlFor="inputUsertype" className="">
             
          </label>
          <select
            name="usertype"
            onChange={onHandleChange}
            id="inputUsertype"
            className="form-control text-right"
            placeholder=" "
            defaultValue="-1"
            required
          >
            <option value="-1" disabled hidden>
               
            </option>
            <option value="super"></option>
            <option value="admin"></option>
            <option value="agent"> </option>
          </select>
        </div>
        <div className="col">
          <label htmlFor="inputUsername" className="">
             
          </label>
          <input
            name="username"
            onChange={onHandleChange}
            type="text"
            id="inputUsername"
            className="form-control text-right"
            placeholder=" "
            required
          />
        </div>
      </div>
      <div className="row text-right">
        <div className="col order-last">
          <label htmlFor="inputPassword" className="">
             
          </label>
          <input
            name="password"
            onChange={onHandleChange}
            type="password"
            id="inputPassword"
            className="form-control text-right"
            placeholder=" "
            required
          />
        </div>
        <div className="col">
          <label htmlFor="inputRepassword" className="">
              
          </label>
          <input
            name="repassword"
            onChange={onHandleChange}
            type="password"
            id="inputRepassword"
            className="form-control text-right"
            placeholder="  "
            required
          />
        </div>
      </div>
      <div className="row text-right">
        <div className="col">
          <label htmlFor="inputEmail" className="">
              
          </label>
          <input
            name="email"
            onChange={onHandleChange}
            type="email"
            id="inputEmail"
            className="form-control text-right"
            placeholder=" "
            required
          />
        </div>
      </div>

      <div className="row">
        <div className="col">
          <ScanedImage
            imageLink={utilisateur.photo_link}
            title=" "
          />
        </div>
        <div className="col-9">
          {utilisateur.photo_link === DefaultImage ? (
            <FileUpload
              Num_Dossier={utilisateur.username}
              titleFilename=" "
              tosendFilename={"photo_profile_" + utilisateur.username + ".jpg"}
              setLink={setlink}
              scanelement={"photo_link"}
            />
          ) : null}
        </div>
      </div>
      <div className="row">
        <div className="col">
          <label htmlFor="inpuRemark" className="">
            {" "}
          </label>
          <input
            name="remark"
            onChange={onHandleChange}
            type="text"
            id="inputRemark"
            className="form-control text-right"
            placeholder=" "
          />
        </div>
      </div>
      <hr />
      <div className="row">
        <div className="col order-last">
          <input
            className="btn btn-lg btn-primary btn-block"
            type="submit"
            value="   "
          />
        </div>
        <div className="col">
          <input
            className="btn btn-lg btn-primary btn-block"
            type="reset"
            value=""
          />
        </div>
      </div>

      <p className="mt-5 mb-3 text-muted"> 2021 hmdmok</p>
    </form>
  );
}

export default Utilisateur;
