import React, { useEffect, useState } from "react";
import Logo from "../Logo/Logo";
import file from "./file.png";
import demandeur from "./demandeur.png";
import update from "./update.png";
import DefaultImage from "../ScanDossier/DefaultImage.png";

function DisplayForm(props) {
  const userID = props.getUserid();
  const [user, setUser] = useState({});
  useEffect(() => {
    fetch("http://localhost:3005/DisplayForm/" + userID)
      .then((response) => response.json())
      .then((data) => {
        setUser(data);
      })
      .catch((err) => console.log(err));
  }, [userID]);
  return (
    <div className="container border shadow p-4 mb-3 mt-3 bg-body rounded">
      <div className="m-2 row d-flex justify-content-center flex-row-reverse">
        <Logo root={"/DisplayForm"} title={user.user_name} pic={user.photo_link} />
      </div>
      <div className="d-flex justify-content-center">
        <h1 className="col-12"> </h1>
      </div>
      <div className="row d-flex justify-content-center flex-row-reverse">
        <div className="m-2">
          <Logo root={"/Dossier"} title={" "} pic={file} />
        </div>
        <div className="m-2">
          <Logo root={"/ScanDossier"} title={" "} pic={file} />
        </div>
        <div className="m-2">
          <Logo root={"/Demandeur"} title={" "} pic={demandeur} />
        </div>
        <div className="m-2">
          <Logo root={"/Conjoin"} title={" () "} pic={demandeur} />
        </div>
        <div className="m-2">
          <Logo root={"/Demandeur"} title={" "} pic={update} />
        </div>
        <div className="m-2">
          <Logo root={"/Utilisateur"} title={" "} pic={update} />
        </div>
      </div>
    </div>
  );
}

export default DisplayForm;
